.password-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-image: url('../../assets/password/passwordbg.jpg');
    background-size: cover;
    color: #00273C;
    font-family: 'Poppins', sans-serif;

}

h1{
    color: aliceblue;
    font-weight: 900;
    margin-bottom: 30px;
    font-size: 35px;
}

.password {
    padding: 20px;
    width: 80%;
    max-width: 400px;
    background-color: #96fbf6d0;
    border-radius: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); /* Add shadow effect */
}

.pwd-length {
    width: 100%;
}

.pwd-length input{
    padding: 10px;
    width: 100%;
    border: none;
    border-radius: 10px;
}

.pwd-length input:focus {
    outline: none; /* Remove the default browser outline */
}

.fields{
    display: flex;
    flex-direction: column;
    padding: 10px 30px;
    width: 100%;
    margin: 20px 0;
}

.fields input{
    margin: 10px 0;
    cursor: pointer;
}

.fields label{
    display: flex;
    align-items: center;
}

.fields p{
    margin-left: 10px;
    font-size: 20px;
}

.btn{
    width: 100%;
    display: flex;
    justify-content: center;
}

.btn button{
    padding: 10px 20px;
    background-color: #00273C;
    color: #96fbf6d0;
    border: none;
    border-radius: 20px;
    cursor: pointer;
}

.generated-pwd {
    display: flex;
    align-items: center;
}

h3{
    margin: 30px 10px;
    font-size: 15px;
}


@media (max-width: 768px) {

    .password-container{
        justify-content: start;
    }

    .password-container h1{
        margin-top: 50px;
        font-size: 25px;
    }

    .fields p{
        margin-left: 10px;
        font-size: 15px;
    }

    .generated-pwd {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    h3{
        margin-bottom: 20px;
    }
}


